/* @import url("https://cdnjs.cloudflare.com/ajax/libs/semantic-ui/2.4.1/semantic.min.css"); */

.App {
  /* background-color: rgba(1, 1, 1, 0.4); */
  max-width: 100%;
  max-height: 100vh;
}

.image__slider {
  position: absolute;
  top: 0;
  left: 0;

  display: -webkit-flex;

  display: flex;
  overflow-y: hidden;
  overflow-x: scroll;
  padding: 0px 16px;

  z-index: 3;

  background-color: rgba(0, 0, 0, 0.85);

  height: 100vh;
  width: 100vw;
  min-height: 100%;
}

.image__slider::-webkit-scrollbar {
  display: none;
}

.image__showcase {
  margin: 1em 0px;
  border-radius: 10px;
  background-color: #fff;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  overflow: hidden;
  min-width: 18em;
  max-height: 38em;

  transition: -webkit-transform 450ms ease;

  transition: transform 450ms ease;

  transition: transform 450ms ease, -webkit-transform 450ms ease;
}

.image__showcase .gradient {
  position: absolute;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.4);
  height: 100%;
  width: 100%;
  display: block;
  opacity: 1;
}

.image__showcase.vis {
  -webkit-transform: scale(1);
          transform: scale(1);
}

.image__showcase.vis .gradient {
  transition: all 300ms ease;
  opacity: 0;
}

.image__showcase.invis {
  -webkit-transform: scale(0.9);
          transform: scale(0.9);
  transition: all 800ms ease;
}

.image__showcase.invis .gradient {
  opacity: 1;
}

.image__container {
  overflow: hidden;
}

.image__content {
  padding: 0.5em 0.9em;
  text-align: left;
  font-family: "Roboto", sans-serif;
}

